@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&family=Open+Sans+Condensed:wght@300&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: #333;
  font-family: 'Montserrat', sans-serif;
}

.App {
  min-height: 100vh;
  background-color: #eff7e2;
}

.spinner {
  display: block;
  margin: 0 auto;
}

.search-form {
  width: 100%;
  position: relative;

  min-height: 10vh;  
  padding: 80px;
}

.search-form::before {
  content: "";
  background-image: url('./assets/images/banner-image.jpg');
  background-position: center;
  background-size: cover;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.80;
}

.search-form-content {
  display: flex;
  justify-content: center;
  align-items: center;  
  z-index: 999;
  position: relative;
  max-width: 760px;
  margin: 0 auto;
}

.search-bar {
  flex-grow: 1;
  border: none;
  padding: 10px;
  height: 40px;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}

.search-btn {
  font-size: 16px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;

  background-color: #95CD41;
  border: none;
  padding: 0 20px;
  height: 40px;
  color:  white;  
}

.recipes {
  padding: 20px; /* Add spacing around recipes container for small viewports */
  max-width: 1280px; /* Limit recipes width so content looks better on large viewports */
  width: 100%;
  margin: 0 auto; /* Center recipes on large viewports */

  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  align-items: center;
}

/* MEDIA QUERIES */

@media (max-width: 1200px) {
  .recipes, .search-form {
    padding: 35px;
  }

}

@media (max-width: 992px) {
  .recipes, .search-form {
    padding: 45px;
  }
}

@media (max-width: 768px) {
  .recipes, .search-form {
    padding: 55px;
  }
}

@media (max-width: 576px) {
  .recipes, .search-form {
    padding: 40px 60px;
  }
}

@media (max-width: 455px) {
  .recipes, .search-form {
    padding: 20px 40px;
  }
}

@media (max-width: 375px) {
  .search-btn {
    font-size: 14px;
  }
}

